import React, { forwardRef } from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';
import ButtonLabel from '../button-label';

const CSS_PREFIX = 'vds-btn-icon';
const CSS_STATE = 'vds-state';

const ButtonIcon = forwardRef(({
  active,
  ariaLabel,
  children,
  className,
  disabled,
  iconType,
  type,
  ...remainingProps
}, ref) => (
  <button
    aria-label={ariaLabel}
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${iconType}`]: Boolean(iconType),
      [`${CSS_STATE}--active`]: Boolean(active),
    }, className)}
    disabled={disabled}
    ref={ref}
    type={type}
    {...remainingProps}
  >
    <ButtonLabel>{children}</ButtonLabel>
  </button>
));

ButtonIcon.propTypes = {
  /**
   * Active
   */
  active: bool,

  /**
   * Button aria label
   */
  ariaLabel: string,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Icon type
   */
  iconType: oneOf([
    'dark',
    'dark-outline',
    'dark-tiny',
    'light',
    'light-fill',
    'light-outline',
    'light-tiny'
  ]),

  /**
   * @ignore
   */
  type: string
};

ButtonIcon.defaultProps = {
  type: 'button'
};

ButtonIcon.displayName = 'ButtonIcon';

export default ButtonIcon;
