import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-btn-group';

const ButtonGroup = ({ children, className, direction, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}--${direction}`]: Boolean(direction)
    }, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

ButtonGroup.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Direction
   */
  direction: oneOf([
    'reverse',
    'right'
  ])
};

ButtonGroup.displayName = 'ButtonGroup';

export default ButtonGroup;
